.main .about {
	background-color: var(--firstColor);
	font-weight: lighter;
	line-height: 30px;
	font-size: 18px;
	color: rgb(255,255,255);
	border-bottom: 1px solid rgb(101,109,128);
}

.main .about .container {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;
}

.main .about .container header {
	width: 100%;
}

.main .about .about-text {
	width: 60%;
}

.main .about .about-img {
	width: 220px;
	height: 220px;
	border-radius: 50%;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	transition: 0.3s;
	background: 
	linear-gradient(
		360deg, 
		var(--secondColor), 
		transparent,
		transparent
	);

}

.main .about .about-img img {
	height: calc(100% - 12px);
	width: calc(100% - 12px);
	object-fit: cover;
	border-radius: 50%;
}