#meucarrossel {position: relative;}

#meucarrossel .slick-arrow {
	background-color: transparent;
	position: absolute;
	z-index: 999;
	width: 50px;
	height: 50px;
	font-size: 20px;
	border-radius: 50%;
	overflow: hidden;
	border: 0;
	top: 50%;
	transform: translateY(-50%);
	border: 1px solid #fff;
	color: #fff;
	border: 0;
	cursor: pointer;
}

#meucarrossel .slick-dots {
	position: absolute;
	left: 50%;
	bottom: -40px;
	transform: translateX(-50%);
	display: flex;
	gap: 10px;
}

#meucarrossel .slick-dots li button{
	width: 13px;
	height: 13px;
	border-radius: 50%;
	border: 1px solid #fff;
	background-color: transparent;
	overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}


#meucarrossel .slick-dots li button::after {
    content: '';
    background: var(--firstColor);
    display: block;
    position: absolute;
    inset: 0 0 0 0;
    border-radius: 50%;
}

#meucarrossel .slick-dots li.slick-active button::after {
	background: #fff;
}

#meucarrossel .slick-prev {
	left: -55px;
}

#meucarrossel .slick-next {
	right: -55px;
}

#meucarrossel .slick-track {
    gap: 20px;
    display: flex;
    justify-content: space-between;
}

