.main .project {
	background-color: var(--firstColor);
	border-bottom: 1px solid rgb(101,109,128);
}

.main .project .container {
	display: flex;
	flex-wrap: wrap;
}

.main .project .container header {
	width: 100%;
}

.main .project .container .box-project {
	width: 100%;
	display: flex;
	justify-content: space-between;
}

.main .project .container .box-project .project-card {
	width: 300px;
	height: 230px;
	display: flex;
	flex-direction: column;
}

.main .project .box-project .project-card .project-card-img {
	width: 100%;
	height: calc(100% - 30px);
	position: relative;
	overflow: hidden;
	transition: 0.3s;
}

.main .project .box-project .project-card .project-card-img .project-card-link {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	inset: 0 0 100% 0;
	opacity: 0;
	pointer-events: none;
	transition: 0.3s;
	background-color: rgba(0, 0, 0, 0.7);
}

.main .project .box-project .project-card .project-card-img:hover .project-card-link {
	inset: 0 0 0 0;
	opacity: 1;
	pointer-events: auto;
}

.main .project .box-project .project-card .project-card-img .project-card-link a {
	width: 40px;
	height: 40px;
	background-color: var(--secondColor);
	border-radius: 50%;
	display: grid;
	place-items: center;
	color: #fff;
}


.main .project .box-project .project-card .project-card-img img{
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.main .project .box-project .project-card .project-card-footer {
	height: 30px;
	display: flex;
	justify-content: space-between;
	color: rgba(255, 255, 255, 0.8);
	background-color: rgba(255, 255, 255, 0.05);
	padding: 0 5px;
	align-items: center;
}