.header {
	background-color: var(--firstColor);
	border-bottom: 1px solid rgb(101,109,128);
}

.header .container {
	height: 60px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	color: rgb(255, 255, 255);
}

.header .header-navbar > ul {
	display: flex;
	gap: 20px;
	align-items: center;
}

.header .header-navbar > ul .navbar-line {
	display: block;
	background-color: var(--fourthColor);
	height: 30px;
	width: 1px;
}

.header .header-navbar > ul a {
	color: rgb(255, 255, 255);
}

.header .header-navbar > ul > li > a {
	color: rgb(255, 255, 255,0.8);
	padding: 0 4px;
	padding-bottom: 18px;
	font-size: 18px;
	font-weight: lighter;
}

.header .header-navbar > ul > li >  a.active,
.header .header-navbar > ul > li > a:hover
{
	color: rgb(255, 255, 255);
	border-bottom: 2px solid rgb(101,109,128);

}


.header .header-navbar > ul .header-button {
	background-color: var(--thirdColor);
	padding: 6px 12px;
	border-radius: 3px;
	text-transform: uppercase;
	font-size: 16px;
}

.header .header-navbar > ul > li {
	position: relative;
}

.header .header-navbar ul li:hover > .header-dropmenu {
	visibility: visible;
	transition: 0.3s;
	top: 40px;
	opacity: 1;
}

.header .header-navbar ul li > .header-dropmenu {
	position: absolute;
	top: 50px;
    left: 0;
    border-top: 2px solid #fff;
    width: 183px;
    visibility: hidden;
    opacity: 0.5;
}

.header .header-navbar ul li > .header-dropmenu li:first-child a{
	border-bottom: 1px solid var(--firstColor);
}

.header .header-navbar ul li > .header-dropmenu a {
	display: block;
	padding: 10px;
    background-color: var(--firstColor);
}

.header .header-navbar ul li > .header-dropmenu a:hover {
	background-color: rgb(61, 77, 69);
}

