* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Opens sans', 'Roboto';
}

:root {
	--firstColor: rgb(23,26,29);
	--secondColor: rgb(76, 114, 191);
	--thirdColor: rgb(76,191,107);
	--fourthColor: rgb(120,120,120);
}

.container {
	width: 80%;
	margin: 0 auto;
}

li {list-style-type: none;}
a {text-decoration: none;}

header h1 {
	margin-bottom: 38.5px;
	font-weight: 300;
	color: rgb(255,255,255);
	font-size: 30px;
}

header h1 span.jogo-da-velha {
	color: var(--secondColor);
	margin-right: 10px;
	font-weight: 500;
}

.pdg {
	padding: 4em 0;
}