.main .tecnology {
	background-color: var(--firstColor);
	border-bottom: 1px solid rgb(101,109,128);
}

.main .tecnology .container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	gap: 40px 20px;

}

.main .tecnology .container header {
	width: 100%;
}

.main .tecnology .container .tecnology-card {
	width: 277px;
	height: 380px;
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.main .tecnology .container .tecnology-card .tecnology-card-up {
	height: 180px;
	background-color: rgba(255,255,255,0.1);
	display: flex;
	justify-content: center;
	align-items: center;
}

.main .tecnology .tecnology-card .tecnology-card-up .tecnology-card-up-img {
	width: 120px;
	height: 120px;
	background: linear-gradient(180deg, #000, transparent);
	border-radius: 50%;
	display: flex;
    align-items: center;
    justify-content: center;
    font-size: 70px;
}

.main .tecnology .container .tecnology-card .tecnology-card-down {
	height: 200px;
	background-color: rgba(255,255,255,0.05);
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 10px;
}

.main .tecnology .tecnology-card .tecnology-card-down table {
	height: 110px;
    width: 100%;
    color: #fff;
}

.main .tecnology .tecnology-card .tecnology-card-down table .optionright {
	text-align: right;
	opacity: 0.8;
}

.main .tecnology .tecnology-card .tecnology-card-down button {
	width: 100%;
	height: 40px;
	border-radius: 5px;
	border: 0;
	text-transform: uppercase;
	background-color: rgba(0, 0, 0, 0.7);
	color: rgb(255, 255, 255);
	opacity: 0.7;
	cursor: pointer;
}

.main .tecnology .tecnology-card .tecnology-card-down button:hover  {
	opacity: 1;
}

