.main .contact {
	background-color: var(--firstColor);
}

.main .contact .container {
	display: flex;
	flex-wrap: wrap;

}

.main .contact .container header {
	width: 100%;
}

.main .contact .container .contact-form .box-input {
	max-width: 395px;
	width: 395px;
	height: 50px;
	border: 1px solid rgb(255,255,255);
	border-radius: 3px;
	margin-bottom: 20px;
	position: relative;
	font-family: 'Open sans' !important;
}

.main .contact .container .contact-form .box-input label {
	position: absolute;
	background-color: var(--firstColor);
	top: 50%;
	left: 10px;
	transform: translateY(-50%);
	z-index: 1;
	color: rgb(255, 255, 255);
	padding: 0 3px;
	transition: 0.3s all;
	font-family: 'Open sans' !important;
}

.main .contact .container .contact-form .box-input input {
	width: 100%;
	height: 100%;
	
	outline: none;
	border: 0;
	z-index: 999;
	background-color: transparent;
	color: rgb(255, 255, 255);
	padding: 0 14px;
}

.main .contact .container .contact-form .box-input input:focus ~ label,
.main .contact .container .contact-form .box-input input:valid ~ label {
	top: -10px;
	transform: translate(0);
}

.main .contact .container .contact-form button {
	width: 395px;
	height: 40px;
	font-family: 'roboto';
	font-size: 14px;
	text-transform: uppercase;
	background-color: var(--secondColor);
	border: 0;
	border-radius: 2px;
	color: rgb(255, 255, 255);
}